* {
  font-family: 'Raleway', sans-serif;
}

body {
  background-color: #ebebeb
}

//top
.top {
  margin-top: 20px;
  margin-bottom: 10px;
}

.blue_button {
  border-radius: 6px;
  background-color: rgb(80,197,218);
  width: 166px;
  height: 30px;
  padding: 0;
  color: white;
}

#glass, #srch-term {
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 0;
  height: 30px;
}

#glass {
  margin-right: -10px;
}

#srch-term::placeholder {
  opacity: 0.5;
}

#glass img {
  margin: -8px;
  margin-top: -15px;
}



//navbar

.navbar-nav, .navbar-brand {
  margin-top: 30px;
  margin-bottom: 10px;
}

.nav-link {
  color: #5b5a5a;
}
.nav-link:hover, .dropdown-item:hover, .navbar-brand:hover {
  color: #096c7e;
}

.navbar-brand {
  font-family: Times New Roman;
  font-size: 22px;
  color: black;
}

.vl {
  border-left: 1px solid lightgrey;
  height: 25px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 1%;
}

//.navbar-nav > li > .dropdown-menu { background-color: #FF0000; }

//main section

.arrow-down {
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #f00;
  display: inline-block;
}

.blueStrip {
  height: 23px;
  background-color: #50c5da;
  margin-top: -15px;
}

/*
.triangles {
  width: 1103px;
}
*/

.mainImg, .triangles {
  width: 100%;
}

.triangles {
  margin-top: -15px;
}

.mainImg {
  height: 450px;
}
//main button
.blue {
  border-radius: 9px;
  background-color: rgb(80,197,218);
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.23);
  max-width: 401px;
  height: 201px;
  z-index: 3;
  margin-top: -360px;
  position: relative;

}

.blue p, h3 {
  color: white;
}

.blue p {
  margin-bottom: 0;
  position: absolute;
  margin-top: 10%;
  margin-left: 10%;
}

.blue h3 {
  position: absolute;
  margin-top: 17%;
  margin-left: 10%;
  font-weight: bold;
}

.check {
  border-radius: 12px;
  background-color: rgb(39,39,39);
  width: 195px;
  height: 45px;
  color: white;
  margin-top: 145px;
  margin-left: 10%;
}

.check:hover {
  color: #50c5da;
}

//part with keys & slider

.emptyRow {
  height: 50px;
  background-color: white;
}
.sliderSection {
  background-color: white;
}

//suwak

.slider {
  -webkit-appearance: none;
  width: 100%;
  background: #d3d3d3;
  outline: none;
  -webkit-transition: .2s;
  transition: opacity .2s;
  opacity: 0.651;
  height: 20px;

}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  background-image: url("../images/three.png");
  background-size: 45% 30%;
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid rgb(255,255,255);
  border-radius: 8px;
  background-color: rgb(80, 197,218);
  width: 61px;
  height: 36px;

}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #4CAF50;
  cursor: pointer;
}


.slidecontainer p {
  margin-bottom: 5px;
}

#rata, #defaultRata, .link {
  color: #50c5da;
}

#rata, #defaultRata {
  font-weight: bold;
  font-size: 150%;
}

//blue section

.blueRow {
  height: 60px;
  background-color: #50c5da;
}

.bl {
  background-color: #50c5da;
}

.blueimg {
  margin-bottom: 30px;
}

.bluediv p {
  color: white;
  margin-top: -8px;
}

.transparentBtn {
  border: 1px solid rgb(255, 255, 255);
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0);
  width: 235px;
  height: 50px;
  color: white;
  font-size: 20px;
}

.transparentBtn:hover {
  color: darkblue;
}

//three sectors

.white {
  background-color: white;
}
.whiteRow {
  height: 40px;
  background-color: white;
}

.side {
  width: 5%;
}

.oneSection h5 {
  margin-left: 7%;
  font-weight: bold;
  color: #717070;
}

.oneSection ul li a {
  color: grey;
  margin-left: 10px;
}

.oneSection ul li {
  margin-bottom: 8px;
}

.oneSection ul {
  list-style-image: url("../images/square.svg");
}